/* --------------顏色設定開始--------------- */
$primary-color:#4EB531;
$secondary-color:#E67B44;
$tertiary-color:#AA6EAC;
$tertiary-light-color:#E5D3E6;
$assist-color:#51A6AB;
$assist-light-color:#D0EAEC;
$cold-color:#448FE5;
$cold-light-color:#C6DDF7;
$light-color:#E8EEF2;
$steady-color:#C99E5E;
$steady-light-color:#F0E7D9;
$white:#fff;
$gray:#ccc;
$gray-dark:#666666;
$gray-light:#F8F8F8;


$themes: (
  //屬性: key, value
  'primary-color': $primary-color,
  'secondary-color': $secondary-color,
  'tertiary-color':$tertiary-color,
  'tertiary-light-color':$tertiary-light-color,
  'assist-color': $assist-color,
  'assist-light-color': $assist-light-color,
  'cold-color':$cold-color,
  'cold-light-color':$cold-light-color,
  'light-color':$light-color,
  'steady-color':$steady-color,
  'steady-light-color':$steady-light-color,
  'white':$white,
  'gray':$gray,
  'gray-light':$gray-light,
);

/* --------------字型設定開始--------------- */
$font-xs:.7rem;
$font-sm:.875rem;
$font-md:1rem;
$font-ml:1.1rem;
$font-lg:1.5rem;
$font-xl:1.8rem;
$font-xxl:3rem;

$font-xlight:100;
$font-light:300;
$font-bold:500;
$font-heavy:700;

$font-sizes: (
  //屬性: key, value
  'font-xs': $font-xs,
  'font-sm': $font-sm,
  'font-md': $font-md,
  'font-ml': $font-ml,
  'font-lg': $font-sm,
  'font-xl': $font-xl,
  'font-xxl': $font-xxl,
);
/* --------------其他功能設定開始--------------- */
/*--溢出文字點點點--*/
@mixin over_text($line) {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: $line;
}

/*--隱藏區塊內文字--*/
@mixin text-indent {
    text-indent: 200%;
    overflow: hidden;
    white-space: nowrap;
}

/* --------------按鈕設定開始--------------- */
@mixin btn-dark($bgcolor) {
	color: $white;
  background: $bgcolor;
  font-size: $font-sm;
  font-weight: $font-light;
	&:hover {
    color: $white;
    background: darken($color: $bgcolor, $amount: 5%);
	}
}
@mixin btn-border($bgcolor,$bdcolor){
  color: $gray-dark;
  border: 1px solid $gray-dark;
  padding: 8px 34px;
  font-weight: $font-bold;
  &:hover,&.active{
    color: $white;
    background: $bgcolor;
    border: 1px solid $bdcolor;
  }
}
@mixin btn-nobg($color){
  color: $color;
  border: 2px solid $color;
  padding: 12px 0;
  text-align: center;
  width: 186px;
  font-size: $font-sm;
  @include PC{
    padding: 10px 0;
    width: 140px;
  }
  &:hover{
    background: $color;
    color: $white;
  }
}
/*標題設定開始*/
@mixin top-title($color) {
  font-size: $font-xl;
  letter-spacing: 8px;
  font-weight: 600;
  color:$color;
  position: relative;
  text-align: center;
  padding-bottom: 12px;
  margin-bottom: 30px;
  @include iPad{
    font-size:$font-lg;
    margin-bottom: 22px;
  }
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: $color;
    left: 0;
    bottom: 0;
  }
}
/*表格設定開始*/
@mixin table-simple($bgcolor,$bdcolor){
  font-size: $font-sm;
  border: 2px solid $bdcolor;
  th{
      background: $bgcolor;
      border-bottom: 2px solid $bdcolor!important;
      color: $white;
      font-weight: $font-light;
      @include iPad{
        padding: .5rem;
      }
  }
  td{
    border-top: 1px solid $bdcolor;
    @include iPad{
      padding: .5rem;
    }
  }
  td,th{
    border-color: $bdcolor;
  }
}