//容器設定
.container{
    width: 1000px;
    display: flex;
    padding: 25px;
}
//文字設定
.text-center{
    text-align: center;
}
.font-heavy {
    font-weight: $font-heavy;
}
.font-light {
    font-weight: $font-light;
}
.small-txt{
    font-size: $font-sm;
    line-height: 1.3;
}
@each $key, $value in $themes {
    .#{$key} {
        color: $value!important;
    }
}
@each $key, $value in $font-sizes {
    .#{$key} {
        font-size:  $value!important;
    }
}
//連結設定
.img-link{
    &:hover{
        opacity: 0.8;
    }
}
//背景顏色設定
@each $key, $value in $themes {
    .bg-#{$key} {
        background: $value;
    }
}
//框線設定
@each $key, $value in $themes {
    .border-#{$key} {
        border: 1px solid $value;
    }
}
//區塊設定
.info-card{
    border-radius: 5px;
    box-shadow: 0 2px 5px $gray;
    overflow: hidden;
    margin: 0 6px 0 6px;
    width: 100%;
    .info-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 13px 20px;
        font-size: 1.2em;
    }
    .info-body{
        display: flex;
        padding: 20px 10px;
    }
    .info-txt{
        font-size: 0.6em;
    }
    .info-badge{
        border-radius: 20px;
        padding: 0 5px;
    }
    .info-item{
        width: 100%;
    }
    .info-icon{
        border-radius: 10px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        padding: 8px 0;
        margin: 0 8px 7px 8px;
        font-size: 0.8em;
        &+p{
            font-size: 1em;
            span{
                font-size: 0.8em;
            }
        }
        img{
            width: 37px;
        }
    }
}
